import React, { useState } from 'react';
import { AppBar, Toolbar, Button, Box, IconButton, Divider, Drawer, List, ListItem, Hidden, Container } from '@material-ui/core';
import MenuIcon from '@material-ui/icons/Menu';
import { useTheme } from '@material-ui/core/styles';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import { Link } from 'gatsby';


const navItems = ["Economy", "Art", "Policy", "Ethics", "Snt", "Environment"];

const DoubleHeader = () => {
  const [mobileOpen, setMobileOpen] = useState(false);
  const [hoverStates, setHoverStates] = useState(Array(navItems.length + 4).fill(false));
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'));

  const handleDrawerToggle = () => {
    setMobileOpen(!mobileOpen);
  };

  const handleMouseEnter = index => {
    setHoverStates(prevState => prevState.map((hoverState, i) => i === index ? true : hoverState));
  };

  const handleMouseLeave = index => {
    setHoverStates(prevState => prevState.map((hoverState, i) => i === index ? false : hoverState));
  };

  const buttonStyle = index => ({
    backgroundColor: index < 4 ? '#2f6697' : 'black',
    color: 'white',
    padding: '2px 10px',
    margin: '5px',
    textTransform: 'none',
    textDecoration: hoverStates[index] ? 'underline' : 'none',
    transition: 'all .3s ease'
  });

  const drawer = (
    <div>
      <List>
        {navItems.map((text, index) => (
          <ListItem button key={text}>
            <Button
              component={Link}
              to={`/${text.toLowerCase()}`}
              style={buttonStyle(index + 4)}
              onMouseEnter={() => handleMouseEnter(index + 4)}
              onMouseLeave={() => handleMouseLeave(index + 4)}
            >
              {text}
            </Button>
          </ListItem>
        ))}
      </List>
    </div>
  );
  

  return (
    <>
      <Box style={{ position: 'sticky', top: 0, zIndex: 9999 }}>
        <AppBar position="static" color="white" elevation={0} style={{ margin: 0 }}>
          <Container>
            <Toolbar style={{ justifyContent: 'space-between' }}>
              <div style={{ display: 'grid', gridTemplateColumns: 'auto auto auto', alignItems: 'center', gap: '5px' }}>
                <Button 
                  style={buttonStyle(0)}
                  onMouseEnter={() => handleMouseEnter(0)}
                  onMouseLeave={() => handleMouseLeave(0)}
                  component={Link}
                  to="/"
                >
                  Home
                </Button>
                <Divider orientation="vertical" flexItem style={{height: '25px', margin: 'auto 0'}} />

                <Button 
                  style={buttonStyle(1)}
                  onMouseEnter={() => handleMouseEnter(1)}
                  onMouseLeave={() => handleMouseLeave(1)}
                  component="a"
                  href="http://www.abc.com"
                >
                  हिंदी
                </Button>
              </div>
              <div style={{ display: 'grid', gridTemplateColumns: 'auto auto auto', alignItems: 'center', gap: '5px' }}>
                {!isMobile &&
                  <>
                    <Button 
                      style={buttonStyle(2)}
                      onMouseEnter={() => handleMouseEnter(2)}
                      onMouseLeave={() => handleMouseLeave(2)}
                      component={Link}
                      to="/competition"
                    >
                      Competition
                    </Button>
                    <Divider orientation="vertical" flexItem style={{height: '25px', margin: 'auto 0'}} />

                    <Button 
                      style={buttonStyle(3)}
                      onMouseEnter={() => handleMouseEnter(3)}
                      onMouseLeave={() => handleMouseLeave(3)}
                      component={Link}
                      to="/subscription"
                    >
                      Donate
                    </Button>
                  </>
                }
                {isMobile && 
                  <IconButton
                    color="inherit"
                    aria-label="open drawer"
                    edge="start"
                    onClick={handleDrawerToggle}
                  >
                    <MenuIcon />
                  </IconButton>
                }
              </div>
            </Toolbar>
          </Container>
        </AppBar>
      </Box>

      <Hidden smDown>
        <Box style={{ width: 'calc(100% - 20rem)', marginLeft: '10rem' }}>
          <Divider style={{ backgroundColor: 'black', height: '1.1px' }} />
          <AppBar position="static" color="white" elevation={0}>
            <Container>
            <Toolbar style={{ display: 'flex', justifyContent: 'center' }}>
              {navItems.map((text, index) => (
              <Button
                component={Link}
                to={`/${text.toLowerCase()}`}
                style={buttonStyle(index + 4)}
                onMouseEnter={() => handleMouseEnter(index + 4)}
                onMouseLeave={() => handleMouseLeave(index + 4)}
               >
                 {text}
              </Button>
                  ))}
            </Toolbar>
            </Container>
          </AppBar>
        </Box>
      </Hidden>

      <Hidden mdUp>
        <Drawer
          variant="temporary"
          anchor={theme.direction === 'rtl' ? 'right' : 'left'}
          open={mobileOpen}
          onClose={handleDrawerToggle}
          ModalProps={{
            keepMounted: true,
          }}
          PaperProps={{ style: { maxHeight: `${navItems.length * 48}px`, overflow: 'auto' } }}
        >
          {drawer}
        </Drawer>
      </Hidden>
    </>
  );
}

export default DoubleHeader;
