import React, { useState } from 'react';
import { Box, Container, Grid, Typography, Link, InputBase, Button } from '@material-ui/core';
import { Twitter, Instagram, Pinterest } from '@material-ui/icons';
import { Link as GatsbyLink } from 'gatsby';
import Logo from "../svg/civilsedge.svg"; // Adjust the path to match your SVG directory


const Footer = () => {
  const [email, setEmail] = useState('');
  const [error, setError] = useState(null);
  const [hoverIndexABC, setHoverIndexABC] = useState(null);
  const [hoverIndexContact, setHoverIndexContact] = useState(null);

  const MoreFromABCMenu = [
    { name: 'Competition', link: '/competition', isExternal: false },
    { name: 'Subscription', link: '/subscription', isExternal: false },
    { name: 'हिंदी', link: 'https://example.com/hindi', isExternal: true },
    { name: 'Weekly', link: '/weekly', isExternal: false },
  ];
  const ContactMenu = [
    { name: 'About', link: '/about', isExternal: false },
    { name: 'Contact Us', link: '/contactus', isExternal: false },
    { name: 'Help', link: '/help', isExternal: false },
    { name: 'Jobs', link: '/jobs', isExternal: false },
  ];

  const text = "This is some paragraph about the website. It explains the purpose of the website and any other relevant information.";

  const validateEmail = (email) => {
    const re = /\S+@\S+\.\S+/;
    return re.test(email);
  };

  const handleInputChange = (e) => {
    setEmail(e.target.value);
    if (!validateEmail(e.target.value)) {
      setError('Not a valid email');
    } else {
      setError(null);
    }
  };

  const handleButtonClick = () => {
    if (validateEmail(email)) {
      setEmail('');
      setError(null);
      // Handle the email submission here
    } else {
      setError('Please enter a valid email');
    }
  };

  const [hoverIcon, setHoverIcon] = useState(null);


  const logoStyle = {
    width: '20%', // or any percentage that suits your design
    height: 'auto'
  };


  

  return (
    <Box style={{ backgroundColor: '#f5f5f5', padding: 2 }}>
      <Container maxWidth="lg">
        <Grid container spacing={2}>
          <Grid item xs={12} md={6}>

          <GatsbyLink to="/">
            <Logo style={logoStyle} />
          </GatsbyLink>
          
            <Typography variant="body2" color="textSecondary">
              {text}
            </Typography>
            <Box style={{ marginTop: 2 }}>
             <Typography variant="body2" color="textSecondary" style={{ color: 'red' }}>
               To get newsletter:
             </Typography>

              <Box style={{ display: 'flex', alignItems: 'center', marginTop: 1 }}>
                <InputBase
                  id="newsletter-email"
                  value={email}
                  onChange={handleInputChange}
                  placeholder="Email"
                  style={{
                    height: '30px',
                    border: '1px solid #c4c4c4',
                    borderRadius: '4px',
                    paddingLeft: '10px',
                    lineHeight: '30px',
                  }}
                />
                {error && (
                  <Typography variant="body2" style={{ color: 'red' }}>
                    {error}
                  </Typography>
                )}
                <Button
                  variant="contained"
                  onClick={handleButtonClick}
                  style={{
                    marginLeft: 1,
                    backgroundColor: '#2f6697',
                    color: 'white',
                    padding: '2px 10px',
                    margin: '5px',
                    textTransform: 'none',
                    textDecoration: 'none',
                    transition: 'all .3s ease',
                  }}
                >
                  Submit
                </Button>
              </Box>
            </Box>
          </Grid>
          <Grid item xs={6} md={3}>
            <Typography variant="h6" color="textPrimary" gutterBottom>
              More from ABC
            </Typography>
            <Box component="nav">
            {MoreFromABCMenu.map((menuItem, index) => (
  <Box key={index}>
    {menuItem.isExternal ? (
      <Link href={menuItem.link} target="_blank" rel="noopener noreferrer">
        <Typography 
          variant="body2" 
          color="textSecondary"
          style={{ 
            display: 'inline-block',
            borderBottom: hoverIndexABC === index ? '1px solid red' : 'none',
            cursor: 'pointer'
          }}
          onMouseOver={() => setHoverIndexABC(index)}
          onMouseOut={() => setHoverIndexABC(null)}
        >
          {menuItem.name}
        </Typography>
      </Link>
    ) : (
      <GatsbyLink to={menuItem.link}>
        <Typography 
          variant="body2" 
          color="textSecondary"
          style={{ 
            display: 'inline-block',
            borderBottom: hoverIndexABC === index ? '1px solid red' : 'none',
            cursor: 'pointer'
          }}
          onMouseOver={() => setHoverIndexABC(index)}
          onMouseOut={() => setHoverIndexABC(null)}
        >
          {menuItem.name}
        </Typography>
      </GatsbyLink>
    )}
  </Box>
))}

            </Box>
          </Grid>
          <Grid item xs={6} md={3}>
            <Typography variant="h6" color="textPrimary" gutterBottom>
              Contact
            </Typography>
            <Box component="nav">
  {ContactMenu.map((menuItem, index) => (
    <Box key={index}>
      {menuItem.isExternal ? (
        <Link href={menuItem.link} target="_blank" rel="noopener noreferrer">
          <Typography 
            variant="body2" 
            color="textSecondary"
            style={{ 
              display: 'inline-block',
              borderBottom: hoverIndexContact === index ? '1px solid red' : 'none',
              cursor: 'pointer'
            }}
            onMouseOver={() => setHoverIndexContact(index)}
            onMouseOut={() => setHoverIndexContact(null)}
          >
            {menuItem.name}
          </Typography>
        </Link>
      ) : (
        <GatsbyLink to={menuItem.link}>
          <Typography 
            variant="body2" 
            color="textSecondary"
            style={{ 
              display: 'inline-block',
              borderBottom: hoverIndexContact === index ? '1px solid red' : 'none',
              cursor: 'pointer'
            }}
            onMouseOver={() => setHoverIndexContact(index)}
            onMouseOut={() => setHoverIndexContact(null)}
          >
            {menuItem.name}
          </Typography>
        </GatsbyLink>
      )}
    </Box>
  ))}
</Box>

          </Grid>
        </Grid>
        <Box style={{ marginTop: 2 }}>
          <hr />
          <Box style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
            <Typography variant="body2" color="textSecondary">
              © {new Date().getFullYear()} Site Name. All rights reserved.
            </Typography>
            <Box>
      <Link 
        href="https://twitter.com/your-handle" 
        target="_blank" 
        rel="noopener noreferrer" 
        underline="none" 
        onMouseEnter={() => setHoverIcon('Twitter')} 
        onMouseLeave={() => setHoverIcon(null)}
      >
        <Twitter style={{ color: hoverIcon === 'Twitter' ? 'darkred' : 'red' }} />
      </Link>
      <Link 
        href="https://instagram.com/your-handle" 
        target="_blank" 
        rel="noopener noreferrer" 
        underline="none" 
        onMouseEnter={() => setHoverIcon('Instagram')} 
        onMouseLeave={() => setHoverIcon(null)}
      >
        <Instagram style={{ color: hoverIcon === 'Instagram' ? 'darkred' : 'red' }} />
      </Link>
      <Link 
        href="https://pinterest.com/your-handle" 
        target="_blank" 
        rel="noopener noreferrer" 
        underline="none" 
        onMouseEnter={() => setHoverIcon('Pinterest')} 
        onMouseLeave={() => setHoverIcon(null)}
      >
        <Pinterest style={{ color: hoverIcon === 'Pinterest' ? 'darkred' : 'red' }} />
      </Link>
    </Box>
          </Box>
        </Box>
      </Container>
    </Box>
  );
};

export default Footer;
