import React from "react";
import { Link } from "gatsby";
import Button from "@material-ui/core/Button";
import Grid from "@material-ui/core/Grid";
import Box from "@material-ui/core/Box";
import Typography from "@material-ui/core/Typography";
import useMediaQuery from "@material-ui/core/useMediaQuery";
import { useTheme } from "@material-ui/core/styles";

const Banner = () => {
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down("xs"));

  const bannerStyle = {
    backgroundColor: "#FF5733",
    color: "#fff",
    padding: theme.spacing(1),
    marginBottom: theme.spacing(2),
    height: "50px",
  };

  const textStyle = {
    paddingLeft: theme.spacing(2),
    ...(!isMobile && { paddingLeft: theme.spacing(7) }),
  };

  const buttonStyle = {
    paddingRight: theme.spacing(2),
  };

  const enterButtonStyle = {
    backgroundColor: "#555",
    color: "#FFFFFF",
    "&:hover": {
      backgroundColor: "#000000",
      textDecoration: "underline",
    },
  };

  return (
    <Box style={bannerStyle}>
      <Grid container justify={isMobile ? "flex-start" : "center"} alignItems="center">
        <Grid item xs={8} sm={6} style={textStyle}>
          <Typography variant={isMobile ? "subtitle2" : "h6"}>Join Our Writing Contest Now!</Typography>
        </Grid>
        <Grid item xs={4} sm={2} style={buttonStyle}>
          <Button component={Link} to="/contest" color="inherit" style={enterButtonStyle}>Enter</Button>
        </Grid>
      </Grid>
    </Box>
  );
};

export default Banner;
